.home{
	z-index: 99;
	position: fixed;
	box-sizing: border-box;
	top: 10%;
	bottom: 10%;
	left: 15%;
	right: 15%;
	padding-top: 15px;
	padding-left: 15px;
	padding-right: 15px;
	background-color: #ffffff;
	border-radius: 25px;
	box-shadow: 0 5px 10px rgba(0,0,0,0.25);
}
.home_egg{
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	justify-content: center;
	align-items:center;
	align-content: center;
	background-color: #FFFFFF;
	position: relative;
	width: 100%;
	height: 90%;
	border: 2px solid #b6c1c7;
	border-radius: 15px;
	overflow: hidden;
}
.home_egg_botom{
	position: relative;
	height: 10%;
	display: flex;
	justify-content: center;
	align-items: center;
	align-self: flex-end;
}
.home_egg_botom_button{
	display: flex;
	justify-content: center;
	align-items: center;
	width: 40px;
	height: 40px;
	border: 2px solid #b6c1c7;
	border-radius: 50%;
}
.home_egg_botom_button:hover{
	background-color: #A2B0B8;
}
.home_egg_botom_button:hover .home_egg_botom_button_square{
	border: 1px solid #FFFFFF;
	background-color: #FFFFFF;
}
.home_egg_botom_button:active{
	background-color: #ffffff;
}
.home_egg_botom_button:active .home_egg_botom_button_square{
	border: 1px solid #666666;
	background-color: #666666;
}
.home_egg_botom_button_square{
	border-radius: 3px;
	height: 70%;
	width: 70%;
	border: 2px solid #e3e8ea;
}

