.background {
	position: absolute;
	top: 0px;
	bottom: 0px;
	width: 100%;
	display: flex;
	flex-direction: row;
	flex-wrap: nowrap;
	justify-content: center;
	align-items: center;
	align-content: center;
	/* overflow: hidden; */
	transition: all 0.3s ease;
	border-radius: 15px;
	z-index: 3;
}
.head-background {
	max-height: 100%;
	max-width: 100%;
	flex-basis: 100%;
}
.head-background-vague {
	/*filter:blur(15px);*/
	position: absolute;
	top: 0px;
	bottom: 0px;
	height: 100%;
	width: 100%;
	z-index: 1;
	background: linear-gradient(to left top, rgba(51, 51, 51, 0.8), rgba(255, 255, 255, 0.7));
}
.head_mask {
	width: 30%;
	height: 25%;
	background-color: rgba(255, 255, 255, 0.8);
	position: absolute;
	z-index: 5;
	right: 0px;
	display: flex;
	text-align: left;
	flex-direction: row;
	flex-wrap: wrap;
	padding: 0.3125rem;
	justify-content: flex-start;
	align-items: center;
	align-content: center;
}
/*
* 小于700
* */
@media only screen and (max-width: 700px) {
.head_mask {
		width: 100% !important;
		bottom: 0px !important;
		background-color: rgba(255, 255, 255, 0) !important;
}
}
/*
* 大于700小于1200
* */
@media screen and (min-width: 700px) and (max-width: 1200px) {
.head_mask {
		width: 35% !important;
		height: 35% !important;
		background-color: rgba(255, 255, 255, 0.8) !important;
}
}

